.first-container {
  background-color:gray; /* Cambia el color de fondo a gris claro */
  /* Agrega otros estilos según tus necesidades */
  padding: 0px;
}
/* Estilos para el contenedor del botón */
.titulo-container {
  display: flex;
  justify-content: center; /* Centrar horizontalmente */
}
h2 {
  font-size: 24px;
  font-family: 'Pacifico', cursive;
  color: #00FF00;
}
h1 {
  font-size: 28px;
  font-family: 'Cookie';
  color: grey;
  text-align: center;
}
.todo-item:hover {
  transform: translateY(-5px); /* Eleva el elemento */
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2); /* Sombra */
  cursor: pointer;
}
/* Estilos para la lista de elementos */
ul {
  list-style: none;
  padding: 0;
}

li {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  margin-bottom: 10px;
  background: rgba(255, 255, 255, 0.5); /* Fondo semitransparente blanco */
}

/* Estilos para los elementos individuales */
li span {
  margin-right: 10px;
  font-size: 10px;
  cursor: pointer;
}

li p {
  margin: 0;
  flex: 1;
  font-size: 20px;
  font-family: 'Cookie', cursive;
}

/* Estilos para los estados */
.tarea-pendiente {
  color: #333333;
}

.tarea-completada {
  color: green;
  text-decoration: line-through;
}

/* Estilos para el input */
input {
  width: 96%;
  padding: 10px;
  margin-bottom: 8px;
  font-family: 'Cookie';
  font-style: bold;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 22px;
}
input::placeholder {
  color: #aaa;
}
/* Estilos cuando el input está enfocado */
input:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}
/* Estilos para el contenedor del botón de agregar tareas*/
.button-container {
  display: flex;
  justify-content: center; /* Centrar horizontalmente */
  margin-top: -8px; /* Agregar margen superior para separación */
  z-index: 15;
}
/* Estilos para el botón */
button {
  z-index: 16;
  background-color:#a9c895; ;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  font-family: Pacifico;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #689f38;
}

.checked-icon {
  padding: 2px;
  margin-right: 4px;
  cursor: pointer;
}
.cancel-icon {
  cursor: pointer;
}
.next-todo-list-cancel-icon {
  cursor: pointer;
  position: absolute;
  top: 1vh;
  right: 0.5vw;
  height: 24px;
}
.next-todo-list-cancel-icon:hover {
  transform: scale(1.3)
}
.cancel-icon:hover {
  transform: scale(1.3)
}
.checked-icon:hover {
  transform: scale(1.3)
}

.inspirational-quote {
  position: absolute;
  left: 125px;
  top: 30px;
  width: 28%; /* El 2/5 de la pantalla es aproximadamente el 40% */
  background-color: rgba(255, 255, 255, 0.4); /* Color de fondo semitransparente */
  padding: 8px; /* Añade relleno para separar el contenido de los bordes */
  border-radius: 15px; /* Ajusta este valor para redondear más o menos los bordes */
}
.inspirational-quote h1 {
  font-family: 'Caveat Brush';
  color: darkgray;
  font-size: 12px;
}
.inspirational-quote h2 {
  font-family: 'Caveat Brush';
  color: darkgoldenrod;
  font-size: 12px;
}

@media (max-width: 830px) {
  .background-image {
    width: 96vw;
    height: 120vh;
    background-image: url('./assets/TodoImageSM.jpg');
    position: relative;
    z-index: 1;
  }
  .button-container {
    position: relative;
    top: 10vh;
  }
  .inspirational-quote {
    position: relative;
    left: 4px;
    top:0px;
    width: 80%;
    height: 10vh;
  }
  .inspirational-quote h1 {
    font-family: 'Caveat Brush';
    color: gray;
    font-size: 14px;
  }
  .todo-list-container {
    width: 92%; /* Ancho más estrecho en pantallas pequeñas */
    height:80%;
    margin-left: 2vw;
    margin-top: 9vh;
    box-shadow: none; /* Elimina sombra en pantallas pequeñas */
    position: absolute;
    top: 12px;
  }
  .new-todo-list {
    z-index: 8;
    position: absolute;
    top: 52vh;
    left: 45vw;
    transform: translate(-50%, -50%); /* Centra el modal correctamente */
    width: 100%;
    height: 87vh;
    background-color: rgba(106, 108, 108, 0.881); /* Fondo semitransparente */
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-image: url('./assets/book_sheet.jpg');
    z-index: 1;
    background-size: cover;
    border-radius: 8px;
    transition: opacity 0.3s ease; /* Agrega una transición suave a la propiedad de opacidad */
    }
    .new-titulo-container {
      width: 90%;
      margin-top: 3vh;
    }
    .show-new-list-button {
      position: absolute;
      right: -2vw;
      top: -5vh;
      height: 10%;
      width: 20%;
      z-index: 10;
      background-color: lightgoldenrodyellow;
      color: gray;
    }
}

